import {mapState} from 'vuex';
export default{
    props: ['orderList'],
    computed: {
        ...mapState(['global'])
    },
    methods: {
        showOrder(order) {
            // console.log(consumeCode); // 传入的订单码
            this.$router.push({path: `/orderDetails/${order.consumeCode}`, query: {order: order}});
        }
    }
};



